body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  text-align: center;
}

.navbar {
  background: #333;
  color: #fff;
  padding: 10px;
}

.footer {
  background: #f1f1f1;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.home input {
  padding: 10px;
  width: 300px;
  margin: 10px;
}

.home button {
  padding: 10px 15px;
  margin: 10px;
  cursor: pointer;
}

.report-table {
  margin: 20px auto;
  border-collapse: collapse;
  width: 80%;
}

.report-table th,
.report-table td {
  border: 1px solid #ccc;
  padding: 10px;
}

.loader {
  margin: 20px;
  font-weight: bold;
}
